import { Injectable } from '@angular/core'
import { Http, Headers } from '@angular/http'
import 'rxjs/add/operator/toPromise';

@Injectable()

export class RestService {
    constructor(private http: Http){

    }

    getDegreeWeather(city: String){
        const url = 'http://api.openweathermap.org/data/2.5/weather?appid=a5980834c617436c7d1f4d063af8b575&units=metric&q='+ city;
        return this.http.get(url)
        .toPromise().then(res => res.json())
        .then(resJson => resJson.main.temp);
    }

    postToExpress(form){
        const url = 'http://localhost:3000/sign-in';
        const headers = new Headers({ 'Content-Type':'application/json' });
        //const body = JSON.stringify({name : 'Huy Pham'});
        const body = JSON.stringify(form.value);
        return this.http.post(url, body, { headers})
        .toPromise()
        .then(res => res.json());
        //.then(resText => console.log(resText));

    }
}


