import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather.service'

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css']
})
export class WeatherComponent implements OnInit {

  txtCity = '';
  cityName = '';
  temp = null;
  isLoading = false;
  constructor(private weatherService: WeatherService) { }

  ngOnInit() {
    // this.weatherService.getDegreeWeather('Tokyo')
    // .then(temp => console.log(temp))
    // .catch(err => console.log(err));
  }

  getweather(){
    console.log(123123);
    this.isLoading = true;
    this.weatherService.getDegreeWeather(this.txtCity)
    .then(temp => {
      this.temp = temp;
      this.cityName = this.txtCity;
      this.isLoading = false;
    })
    .catch(err => {
      alert('Please check name of city!');
      this.isLoading = false;
      this.cityName = '';
      this.txtCity = '';
    });
  }

  getMessage(){
    if(this.isLoading){
      return 'Loading ...';
    }
    return this.cityName == '' ? 'Enter your city: ' : (this.cityName + ' is now ' + this.temp + ' degree');
  }

}



