import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dictionary',
  templateUrl: './dictionary.component.html',
  styleUrls: ['./dictionary.component.css']
})
export class DictionaryComponent implements OnInit {

  filterStatus ='All';
  isShowForm = false;
  newEn = '';
  newVn = '';
  arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
];

  constructor() { }

  ngOnInit() {
  }

  addWord(){
    //this.arrWords.unshift
    this.arrWords.push({
      id: this.arrWords.length + 1,
      en: this.newEn,
      vn : this.newVn,
      memorized : false
    });

    this.newEn = '';
    this.newVn = '';
    this.isShowForm = false;
  }

  deleteWord(id : number){
      const index = this.arrWords.findIndex(word => word.id === id)
      this.arrWords.splice(index, 1);
  }

  showByStatus(memorized: boolean){
    const allCondition = this.filterStatus === 'All';
    const memorizedCondition = this.filterStatus === 'Memorized' && memorized;
    const notMemorizedCondition = this.filterStatus ==='Not Memorized' && !memorized;
    return allCondition || memorizedCondition || notMemorizedCondition;
    // return allCondition;
  }

}



