import { Component } from "@angular/core";

@Component({
    templateUrl: "./word.component.html",
    selector : "app-word",
    styleUrls: ['./word.component.css']
})

export class WordComponent{
    en = 'Hello';
    vn = 'Xin chao`';
    flag = false;

    toggle(){
        this.flag = !this.flag;
    }
}


